import { create } from 'zustand';
import { subscribeWithSelector } from 'zustand/middleware';
import { persist } from 'zustand/middleware';
import {
  NetworkDevice,
  DeviceFolder,
  NetworkMetrics,
  BandwidthData,
  AlertMessage,
  SystemHealth,
  NetworkConnection,
  MonitoringConfig,
} from '../types/schemas';

interface SystemLog {
  id: string;
  type: 'info' | 'warning' | 'error' | 'success';
  message: string;
  timestamp: Date;
}
import {
  mockDevices,
  mockFolders,
  mockAlerts,
  initialMetrics,
  initialSystemHealth,
  initialBandwidthHistory,
  initialConfig,
} from '../data/mock-data';
import api from '../lib/api';

interface NetworkStore {
  // State
  devices: NetworkDevice[];
  folders: DeviceFolder[];
  metrics: NetworkMetrics;
  bandwidthHistory: BandwidthData[];
  alerts: AlertMessage[];
  systemHealth: SystemHealth;
  connections: NetworkConnection[];
  config: MonitoringConfig;
  systemLogs: SystemLog[];

  // UI State
  selectedFolderId: string;
  sidebarCollapsed: boolean;
  loading: boolean;
  error: string | null;
  rootFolderExpanded: boolean;
  // –õ–æ–∫–∞–ª—å–Ω–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ —Ä–∞—Å–∫—Ä—ã—Ç–∏—è –ø–∞–ø–æ–∫ (–Ω–µ –æ—Ç–ø—Ä–∞–≤–ª—è–µ—Ç—Å—è –Ω–∞ —Å–µ—Ä–≤–µ—Ä)
  folderExpandedState: Record<string, boolean>;

  // API integration flags
  useApi: boolean;
  apiConnected: boolean;

  // WebSocket state
  webSocketConnected: boolean;

  // Device actions
  addDevice: (
    device: Omit<NetworkDevice, 'id' | 'lastSeen' | 'status'>
  ) => Promise<void>;
  updateDevice: (
    deviceId: string,
    updates: Partial<NetworkDevice>
  ) => Promise<void>;
  deleteDevice: (deviceId: string) => Promise<void>;
  pingDevice: (deviceId: string) => Promise<void>;

  // Folder actions
  addFolder: (folder: Omit<DeviceFolder, 'id' | 'children'>) => Promise<void>;
  updateFolder: (
    folderId: string,
    updates: Partial<DeviceFolder>
  ) => Promise<void>;
  deleteFolder: (folderId: string) => Promise<void>;

  // Alert actions
  addAlert: (alert: Omit<AlertMessage, 'id' | 'timestamp'>) => void;
  markAlertResolved: (alertId: string) => void;
  acknowledgeAlert: (alertId: string, userId: string) => void;
  clearAlerts: () => void;

  // System log actions
  addSystemLog: (log: Omit<SystemLog, 'id' | 'timestamp'>) => void;
  clearSystemLogs: () => void;

  // WebSocket actions
  setWebSocketConnected: (connected: boolean) => void;
  updateDeviceStatus: (
    deviceId: string,
    updates: Partial<NetworkDevice>
  ) => void;

  // Folder UI state helpers
  getFolderExpandedState: (folderId: string) => boolean;

  // Data actions
  setBandwidthData: (data: BandwidthData[]) => void;
  addBandwidthData: (data: BandwidthData) => void;
  updateSystemHealth: (health: SystemHealth) => void;
  updateMetrics: (metrics: Partial<NetworkMetrics>) => void;
  loadDevices: () => Promise<void>;
  loadFolders: () => Promise<void>;
  loadSystemHealth: () => Promise<void>;
  loadBandwidthData: () => Promise<void>;
  loadNetworkMetrics: () => Promise<void>;

  // UI actions
  setSelectedFolder: (folderId: string) => void;
  setSidebarCollapsed: (collapsed: boolean) => void;
  setLoading: (loading: boolean) => void;
  setError: (error: string | null) => void;
  setUseApi: (useApi: boolean) => void;

  // Data management
  refreshData: () => Promise<void>;
  exportData: () => string;
  importData: (jsonData: string) => boolean;
  resetData: () => void;
  resetToMockData: () => void;
  setRootFolderExpanded: (expanded: boolean) => void;

  initializeApi: () => Promise<void>;
}

export const useNetworkStore = create<NetworkStore>()(
  subscribeWithSelector(
    persist(
      (set, get) => ({
        // Initial state (devices –∏ folders –ø—É—Å—Ç—ã–µ, useApi –≤—Å–µ–≥–¥–∞ true)
        devices: [],
        folders: [],
        metrics: initialMetrics,
        bandwidthHistory: initialBandwidthHistory,
        alerts: [],
        systemHealth: initialSystemHealth,
        connections: [],
        config: initialConfig,
        systemLogs: [],

        // UI state
        selectedFolderId: 'root',
        sidebarCollapsed: false,
        loading: false,
        error: null,
        rootFolderExpanded: true,
        // –õ–æ–∫–∞–ª—å–Ω–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ —Ä–∞—Å–∫—Ä—ã—Ç–∏—è –ø–∞–ø–æ–∫ (–Ω–µ –æ—Ç–ø—Ä–∞–≤–ª—è–µ—Ç—Å—è –Ω–∞ —Å–µ—Ä–≤–µ—Ä)
        folderExpandedState: {},

        // API integration flags
        useApi: true,
        apiConnected: false,

        // WebSocket state
        webSocketConnected: false,

        // Device actions
        addDevice: async (deviceData) => {
          console.log('üè™ Store: addDevice –≤—ã–∑–≤–∞–Ω —Å –¥–∞–Ω–Ω—ã–º–∏:', deviceData);
          try {
            const response = await api.devices.createDevice(deviceData);
            console.log('‚úÖ –£—Å—Ç—Ä–æ–π—Å—Ç–≤–æ —Å–æ–∑–¥–∞–Ω–æ —á–µ—Ä–µ–∑ API:', response);
            await get().loadDevices();
          } catch (error) {
            console.error('‚ùå –û—à–∏–±–∫–∞ —Å–æ–∑–¥–∞–Ω–∏—è —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞:', error);
            set({ error: '–û—à–∏–±–∫–∞ —Å–æ–∑–¥–∞–Ω–∏—è —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞ —á–µ—Ä–µ–∑ API' });
          }
        },
        updateDevice: async (deviceId, updates) => {
          console.log(
            'üè™ Store: updateDevice –≤—ã–∑–≤–∞–Ω –¥–ª—è ID:',
            deviceId,
            '—Å –¥–∞–Ω–Ω—ã–º–∏:',
            updates
          );
          try {
            const response = await api.devices.updateDevice(deviceId, updates);
            console.log('‚úÖ –£—Å—Ç—Ä–æ–π—Å—Ç–≤–æ –æ–±–Ω–æ–≤–ª–µ–Ω–æ —á–µ—Ä–µ–∑ API:', response);
            await get().loadDevices();
          } catch (error) {
            console.error('‚ùå –û—à–∏–±–∫–∞ –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞:', error);
            set({ error: '–û—à–∏–±–∫–∞ –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞ —á–µ—Ä–µ–∑ API' });
          }
        },
        deleteDevice: async (deviceId) => {
          try {
            await api.devices.deleteDevice(deviceId);
            console.log('‚úÖ –£—Å—Ç—Ä–æ–π—Å—Ç–≤–æ —É–¥–∞–ª–µ–Ω–æ —á–µ—Ä–µ–∑ API');
            await get().loadDevices();
          } catch (error) {
            set({ error: '–û—à–∏–±–∫–∞ —É–¥–∞–ª–µ–Ω–∏—è —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞ —á–µ—Ä–µ–∑ API' });
          }
        },
        pingDevice: async (deviceId) => {
          try {
            const response = await api.devices.pingDevice(deviceId);
            console.log('üèì Ping –≤—ã–ø–æ–ª–Ω–µ–Ω —á–µ—Ä–µ–∑ API:', response);
            if (response && response.data) {
              const pingData = response.data as any;
              const alive = pingData.alive;
              const responseTime = pingData.responseTime || 0;
              await get().updateDevice(deviceId, {
                status: alive ? 'online' : 'offline',
                responseTime: responseTime,
                lastSeen: new Date(),
              });
            }
          } catch (error) {
            set({ error: '–û—à–∏–±–∫–∞ ping —á–µ—Ä–µ–∑ API' });
          }
        },

        // Folder actions
        addFolder: async (folderData) => {
          console.log('üè™ Store: addFolder –≤—ã–∑–≤–∞–Ω —Å –¥–∞–Ω–Ω—ã–º–∏:', folderData);
          try {
            const response = await api.folders.createFolder(folderData);
            console.log('‚úÖ –ü–∞–ø–∫–∞ —Å–æ–∑–¥–∞–Ω–∞ —á–µ—Ä–µ–∑ API:', response);
            await get().loadFolders();
          } catch (error) {
            console.error('‚ùå –û—à–∏–±–∫–∞ —Å–æ–∑–¥–∞–Ω–∏—è –ø–∞–ø–∫–∏:', error);
            set({ error: '–û—à–∏–±–∫–∞ —Å–æ–∑–¥–∞–Ω–∏—è –ø–∞–ø–∫–∏ —á–µ—Ä–µ–∑ API' });
          }
        },

        updateFolder: async (folderId, updates) => {
          // –°–ø–µ—Ü–∏–∞–ª—å–Ω–∞—è –æ–±—Ä–∞–±–æ—Ç–∫–∞ –¥–ª—è –∫–æ—Ä–Ω–µ–≤–æ–π –ø–∞–ø–∫–∏
          if (folderId === 'root') {
            set((state) => ({
              rootFolderExpanded:
                updates.expanded !== undefined
                  ? updates.expanded
                  : state.rootFolderExpanded,
            }));
            return;
          }

          // –ï—Å–ª–∏ –æ–±–Ω–æ–≤–ª—è–µ—Ç—Å—è —Ç–æ–ª—å–∫–æ —Å–æ—Å—Ç–æ—è–Ω–∏–µ expanded, —Å–æ—Ö—Ä–∞–Ω—è–µ–º –ª–æ–∫–∞–ª—å–Ω–æ
          if (
            Object.keys(updates).length === 1 &&
            updates.expanded !== undefined
          ) {
            set((state) => ({
              folderExpandedState: {
                ...state.folderExpandedState,
                [folderId]: updates.expanded!,
              },
            }));
            return;
          }

          // –û–±–Ω–æ–≤–ª—è–µ–º —á–µ—Ä–µ–∑ API (—Ç–æ–ª—å–∫–æ –±–∏–∑–Ω–µ—Å-–¥–∞–Ω–Ω—ã–µ, –Ω–µ UI —Å–æ—Å—Ç–æ—è–Ω–∏–µ)
          console.log(
            'üè™ Store: updateFolder –≤—ã–∑–≤–∞–Ω –¥–ª—è –ø–∞–ø–∫–∏:',
            folderId,
            updates
          );
          try {
            // –ò—Å–∫–ª—é—á–∞–µ–º expanded –∏–∑ –æ—Ç–ø—Ä–∞–≤–∫–∏ –Ω–∞ —Å–µ—Ä–≤–µ—Ä
            const { expanded, ...serverUpdates } = updates;

            if (Object.keys(serverUpdates).length > 0) {
              const response = await api.folders.updateFolder(
                folderId,
                serverUpdates
              );
              console.log('‚úÖ –ü–∞–ø–∫–∞ –æ–±–Ω–æ–≤–ª–µ–Ω–∞ —á–µ—Ä–µ–∑ API:', response);
              await get().loadFolders();
            }

            // –°–æ—Ö—Ä–∞–Ω—è–µ–º —Å–æ—Å—Ç–æ—è–Ω–∏–µ expanded –ª–æ–∫–∞–ª—å–Ω–æ
            if (expanded !== undefined) {
              set((state) => ({
                folderExpandedState: {
                  ...state.folderExpandedState,
                  [folderId]: expanded,
                },
              }));
            }
          } catch (error) {
            console.error('‚ùå –û—à–∏–±–∫–∞ –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è –ø–∞–ø–∫–∏:', error);
            set({ error: '–û—à–∏–±–∫–∞ –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è –ø–∞–ø–∫–∏ —á–µ—Ä–µ–∑ API' });
          }
        },

        deleteFolder: async (folderId) => {
          console.log('üè™ Store: deleteFolder –≤—ã–∑–≤–∞–Ω –¥–ª—è –ø–∞–ø–∫–∏:', folderId);
          try {
            const response = await api.folders.deleteFolder(folderId);
            console.log('‚úÖ –ü–∞–ø–∫–∞ —É–¥–∞–ª–µ–Ω–∞ —á–µ—Ä–µ–∑ API:', response);
            await get().loadFolders();
            await get().loadDevices(); // –ü–µ—Ä–µ–∑–∞–≥—Ä—É–∂–∞–µ–º —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞, —Ç.–∫. –æ–Ω–∏ –º–æ–≥–ª–∏ –±—ã—Ç—å –ø–µ—Ä–µ–º–µ—â–µ–Ω—ã
          } catch (error) {
            console.error('‚ùå –û—à–∏–±–∫–∞ —É–¥–∞–ª–µ–Ω–∏—è –ø–∞–ø–∫–∏:', error);
            set({ error: '–û—à–∏–±–∫–∞ —É–¥–∞–ª–µ–Ω–∏—è –ø–∞–ø–∫–∏ —á–µ—Ä–µ–∑ API' });
          }
        },

        // Alert actions
        addAlert: (alertData) => {
          const alert: AlertMessage = {
            ...alertData,
            id: Math.random().toString(36).substr(2, 9),
            timestamp: new Date(),
          };

          set((state) => ({
            alerts: [alert, ...state.alerts].slice(0, 50), // Keep only last 50 alerts
          }));
        },

        markAlertResolved: (alertId) => {
          set((state) => ({
            alerts: state.alerts.map((alert) =>
              alert.id === alertId ? { ...alert, resolved: true } : alert
            ),
          }));
        },

        acknowledgeAlert: (alertId, userId) => {
          set((state) => ({
            alerts: state.alerts.map((alert) =>
              alert.id === alertId
                ? {
                    ...alert,
                    acknowledgedBy: userId,
                    acknowledgedAt: new Date(),
                  }
                : alert
            ),
          }));
        },

        clearAlerts: () => {
          set({ alerts: [] });
        },

        // Data actions
        setBandwidthData: (data) => {
          set({ bandwidthHistory: data });
        },

        addBandwidthData: (data) => {
          set((state) => ({
            bandwidthHistory: [...state.bandwidthHistory.slice(-19), data],
          }));
        },

        updateSystemHealth: (health) => {
          set({ systemHealth: health });
        },

        updateMetrics: (metricsUpdate) => {
          const state = get();
          const devices = state.devices;

          const totalDevices = devices.length;
          const onlineDevices = devices.filter(
            (d) => d.status === 'online'
          ).length;
          const offlineDevices = devices.filter(
            (d) => d.status === 'offline'
          ).length;
          const warningDevices = devices.filter(
            (d) => d.status === 'warning'
          ).length;
          const averageResponseTime =
            totalDevices > 0
              ? Math.round(
                  devices.reduce((sum, d) => sum + d.responseTime, 0) /
                    totalDevices
                )
              : 0;

          set({
            metrics: {
              ...state.metrics,
              totalDevices,
              onlineDevices,
              offlineDevices,
              warningDevices,
              averageResponseTime,
              lastUpdate: new Date(),
              ...metricsUpdate,
            },
          });
        },

        loadDevices: async () => {
          set({ loading: true, error: null });
          try {
            const response = await api.devices.getDevices();
            let devices: NetworkDevice[] = [];
            if (response && response.data) {
              devices = Array.isArray(response.data) ? response.data : [];
            } else if (Array.isArray(response)) {
              devices = response;
            }
            set({
              devices,
              loading: false,
              apiConnected: true,
              error: null,
            });
            // get().updateMetrics({}); // –£–î–ê–õ–ï–ù–û: –º–µ—Ç—Ä–∏–∫–∏ —Ç–µ–ø–µ—Ä—å —Ç–æ–ª—å–∫–æ –∏–∑ API
          } catch (error) {
            set({
              devices: [],
              loading: false,
              error: 'API –Ω–µ–¥–æ—Å—Ç—É–ø–µ–Ω',
              apiConnected: false,
            });
            // get().updateMetrics({}); // –£–î–ê–õ–ï–ù–û: –º–µ—Ç—Ä–∏–∫–∏ —Ç–µ–ø–µ—Ä—å —Ç–æ–ª—å–∫–æ –∏–∑ API
          }
        },

        loadSystemHealth: async () => {
          try {
            const response = await api.metrics.getSystemHealth();
            console.log('‚úÖ API Response system health:', response);

            // –ü—Ä–æ—Å—Ç–∞—è –æ–±—Ä–∞–±–æ—Ç–∫–∞ - –ø–æ–∫–∞ –∏—Å–ø–æ–ª—å–∑—É–µ–º initialSystemHealth –∏ –æ–±–Ω–æ–≤–ª—è–µ–º apiConnected
            set({
              systemHealth: {
                ...initialSystemHealth,
                lastUpdate: new Date(),
              },
              apiConnected: true,
              error: null,
            });

            console.log('üíª –°–∏—Å—Ç–µ–º–Ω—ã–µ –º–µ—Ç—Ä–∏–∫–∏ –æ–±–Ω–æ–≤–ª–µ–Ω—ã (API –ø–æ–¥–∫–ª—é—á–µ–Ω)');
          } catch (error) {
            console.error('‚ùå API –Ω–µ–¥–æ—Å—Ç—É–ø–µ–Ω –¥–ª—è —Å–∏—Å—Ç–µ–º–Ω–æ–≥–æ –∑–¥–æ—Ä–æ–≤—å—è:', error);
            set({
              systemHealth: initialSystemHealth,
              apiConnected: false,
              error: '–ú–µ—Ç—Ä–∏–∫–∏ —Å–∏—Å—Ç–µ–º—ã –Ω–µ–¥–æ—Å—Ç—É–ø–Ω—ã',
            });
          }
        },

        loadBandwidthData: async () => {
          try {
            const response = await api.metrics.getBandwidthHistory();
            console.log('‚úÖ API Response bandwidth:', response);

            // –û–±—Ä–∞–±–∞—Ç—ã–≤–∞–µ–º –¥–∞–Ω–Ω—ã–µ –ø—Ä–æ–ø—É—Å–∫–Ω–æ–π —Å–ø–æ—Å–æ–±–Ω–æ—Å—Ç–∏
            let bandwidthData = initialBandwidthHistory;
            if (response && response.data && Array.isArray(response.data)) {
              bandwidthData = response.data;
            }

            set({
              bandwidthHistory: bandwidthData,
              apiConnected: true,
            });
            console.log('üìä –î–∞–Ω–Ω—ã–µ –ø—Ä–æ–ø—É—Å–∫–Ω–æ–π —Å–ø–æ—Å–æ–±–Ω–æ—Å—Ç–∏ –æ–±–Ω–æ–≤–ª–µ–Ω—ã');
          } catch (error) {
            console.error(
              '‚ùå API –Ω–µ–¥–æ—Å—Ç—É–ø–µ–Ω –¥–ª—è –¥–∞–Ω–Ω—ã—Ö –ø—Ä–æ–ø—É—Å–∫–Ω–æ–π —Å–ø–æ—Å–æ–±–Ω–æ—Å—Ç–∏:',
              error
            );
            set({
              bandwidthHistory: initialBandwidthHistory,
              apiConnected: false,
            });
          }
        },

        loadFolders: async () => {
          try {
            const response = await api.folders.getFolders();
            console.log('‚úÖ API Response folders:', response);

            if (response && response.data) {
              set({
                folders: response.data,
                apiConnected: true,
              });
              console.log('üìÅ –ü–∞–ø–∫–∏ –∑–∞–≥—Ä—É–∂–µ–Ω—ã —Å API:', response.data.length);
            }
          } catch (error) {
            console.error('‚ùå API –Ω–µ–¥–æ—Å—Ç—É–ø–µ–Ω –¥–ª—è –ø–∞–ø–æ–∫:', error);
            set({
              folders: [],
              apiConnected: false,
            });
          }
        },

        loadNetworkMetrics: async () => {
          try {
            const response = await api.metrics.getNetworkMetrics();
            console.log('‚úÖ API Response network metrics:', response);

            // –û–±—Ä–∞–±–∞—Ç—ã–≤–∞–µ–º –º–µ—Ç—Ä–∏–∫–∏ —Å–µ—Ç–∏
            let metrics = initialMetrics;
            if (response && response.data) {
              metrics = {
                ...initialMetrics,
                ...response.data,
                lastUpdate: new Date(),
              };
            }

            set({
              metrics,
              apiConnected: true,
            });
            console.log('üåê –ú–µ—Ç—Ä–∏–∫–∏ —Å–µ—Ç–∏ –æ–±–Ω–æ–≤–ª–µ–Ω—ã');
          } catch (error) {
            console.error('‚ùå API –Ω–µ–¥–æ—Å—Ç—É–ø–µ–Ω –¥–ª—è –º–µ—Ç—Ä–∏–∫ —Å–µ—Ç–∏:', error);
            set({
              metrics: initialMetrics,
              apiConnected: false,
            });
          }
        },

        // UI actions
        setSelectedFolder: (folderId) => {
          set({ selectedFolderId: folderId });
        },

        setSidebarCollapsed: (collapsed) => {
          set({ sidebarCollapsed: collapsed });
        },

        setLoading: (loading) => {
          set({ loading });
        },

        setError: (error) => {
          set({ error });
        },

        setUseApi: (useApi) => {
          set({ useApi });
        },

        // Data management
        refreshData: async () => {
          set({ loading: true, error: null });
          try {
            await Promise.allSettled([
              get().loadDevices(),
              get().loadSystemHealth(),
              get().loadBandwidthData(),
              get().loadNetworkMetrics(),
            ]);
            set({ loading: false });
          } catch (error) {
            set({
              loading: false,
              error:
                error instanceof Error
                  ? error.message
                  : '–û—à–∏–±–∫–∞ –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è –¥–∞–Ω–Ω—ã—Ö',
            });
          }
        },

        // –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è API –ø–æ–¥–∫–ª—é—á–µ–Ω–∏—è
        initializeApi: async () => {
          console.log('üîå –ü–æ–ø—ã—Ç–∫–∞ –ø–æ–¥–∫–ª—é—á–µ–Ω–∏—è –∫ API...');
          try {
            // –ü—Ä–æ–≤–µ—Ä—è–µ–º –¥–æ—Å—Ç—É–ø–Ω–æ—Å—Ç—å API
            const response = await fetch('http://localhost:5000/api/health');
            if (response.ok) {
              console.log('‚úÖ API –¥–æ—Å—Ç—É–ø–µ–Ω! –í–∫–ª—é—á–∞–µ–º API —Ä–µ–∂–∏–º');
              set({ useApi: true, apiConnected: true });
              await get().loadFolders(); // –ó–∞–≥—Ä—É–∂–∞–µ–º –ø–∞–ø–∫–∏
              await get().refreshData();
            } else {
              console.log('‚ùå API –Ω–µ–¥–æ—Å—Ç—É–ø–µ–Ω, –∏—Å–ø–æ–ª—å–∑—É–µ–º –º–æ–∫–æ–≤—ã–µ –¥–∞–Ω–Ω—ã–µ');
              set({ useApi: false, apiConnected: false });
            }
          } catch (error) {
            console.log('‚ùå API –Ω–µ–¥–æ—Å—Ç—É–ø–µ–Ω, –∏—Å–ø–æ–ª—å–∑—É–µ–º –º–æ–∫–æ–≤—ã–µ –¥–∞–Ω–Ω—ã–µ');
            set({ useApi: false, apiConnected: false });
          }
        },

        exportData: () => {
          const state = get();
          const exportData = {
            devices: state.devices,
            folders: state.folders,
            connections: state.connections,
            config: state.config,
            exportTimestamp: new Date().toISOString(),
          };
          return JSON.stringify(exportData, null, 2);
        },

        importData: (jsonData) => {
          try {
            const data = JSON.parse(jsonData);
            set({
              devices: data.devices || [],
              folders: data.folders || [],
              connections: data.connections || [],
              config: data.config || initialConfig,
            });
            get().updateMetrics({});
            return true;
          } catch {
            return false;
          }
        },

        resetData: () => {
          set({
            devices: [],
            folders: [],
            metrics: initialMetrics,
            bandwidthHistory: [],
            alerts: [],
            systemHealth: initialSystemHealth,
            connections: [],
            selectedFolderId: 'root',
            loading: false,
            error: null,
          });
        },
        resetToMockData: () => {
          // –û—Ç–∫–ª—é—á–µ–Ω–æ: mock-–¥–∞–Ω–Ω—ã–µ –±–æ–ª—å—à–µ –Ω–µ –∏—Å–ø–æ–ª—å–∑—É—é—Ç—Å—è
        },

        setRootFolderExpanded: (expanded) => {
          set({ rootFolderExpanded: expanded });
        },

        // System log actions
        addSystemLog: (log) => {
          const systemLog: SystemLog = {
            ...log,
            id: Date.now().toString(),
            timestamp: new Date(),
          };

          set((state) => ({
            systemLogs: [systemLog, ...state.systemLogs].slice(0, 100), // Keep only last 100 logs
          }));
        },

        clearSystemLogs: () => {
          set({ systemLogs: [] });
        },

        // WebSocket actions
        setWebSocketConnected: (connected) => {
          set({ webSocketConnected: connected });
        },

        updateDeviceStatus: (deviceId, updates) => {
          set((state) => ({
            devices: state.devices.map((device) =>
              device.id === deviceId
                ? { ...device, ...updates, lastSeen: new Date() }
                : device
            ),
          }));
          get().updateMetrics({});
        },

        // Folder UI state helpers
        getFolderExpandedState: (folderId) => {
          const state = get();
          return state.folderExpandedState[folderId] ?? true; // –ü–æ —É–º–æ–ª—á–∞–Ω–∏—é –ø–∞–ø–∫–∏ —Ä–∞–∑–≤–µ—Ä–Ω—É—Ç—ã
        },
      }),
      {
        name: 'network-monitor-storage',
        partialize: (state) => ({
          selectedFolderId: state.selectedFolderId,
          sidebarCollapsed: state.sidebarCollapsed,
          rootFolderExpanded: state.rootFolderExpanded,
        }),
      }
    )
  )
);
